* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.expense-table-container {
  overflow: auto;
  height: 85%;
  width: 80%;
  margin: auto;
}
.expense-table-container.hide,
.paginater-sec.hide {
  display: none;
}

@media screen and (max-width: 1200px) {
  .expense-table-container td:nth-of-type(1):before {
    content: "Sl. NO.";
  }
  .expense-table-container td:nth-of-type(2):before {
    content: "Date";
  }
  .expense-table-container td:nth-of-type(3):before {
    content: "Call Start Time";
  }
  .expense-table-container td:nth-of-type(4):before {
    content: "Call End Time";
  }
  .expense-table-container td:nth-of-type(5):before {
    content: "Reducted Amount";
  }
}
